import React from 'react';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Title from './Title';

// Generate Order Data
function createData(id, date, name, shipTo, paymentMethod, amount) {
  return { id, date, name, shipTo, paymentMethod, amount };
}

const rows = [
  createData(0, '16 Mar, 2019', 'Elvis Presley', 'Gugaon', 'John Smith', 'Done'),
  createData(1, '16 Mar, 2019', 'Paul McCartney', 'Noida', 'John Smith', 'In Progress'),
  createData(2, '16 Mar, 2019', 'Tom Scholz', 'East Delhi', 'John Smith', 'Done'),
  createData(3, '16 Mar, 2019', 'Michael Jackson', 'Gurgaon', 'John Smith', 'In Progress'),
  createData(4, '15 Mar, 2019', 'Bruce Springsteen', 'Noida', 'John Smith', 'In Progress'),
];

function preventDefault(event) {
  event.preventDefault();
}

const useStyles = makeStyles(theme => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
}));

function RecentUpdate() {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Title>Recent Merchants Onboarded</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Date</TableCell>
            <TableCell>Name</TableCell>
            <TableCell>Location</TableCell>
            <TableCell>Agent Name</TableCell>
            <TableCell align="right">Status</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(row => (
            <TableRow key={row.id}>
              <TableCell>{row.date}</TableCell>
              <TableCell>{row.name}</TableCell>
              <TableCell>{row.shipTo}</TableCell>
              <TableCell>{row.paymentMethod}</TableCell>
              <TableCell align="right">{row.amount}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <div className={classes.seeMore}>
        <Link color="primary" href="#" onClick={preventDefault}>
          See more Merchants Onboarded
        </Link>
      </div>
    </React.Fragment>
  );
}

export default RecentUpdate;